@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: "Orbitron", sans-serif, "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: #ffffff;
}

code {
  font-family: "Orbitron", sans-serif;
}

.App {
  text-align: center;
}
#appComponent {
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
  font-family: "Orbitron", sans-serif;
}

#navbarStyling {
  /* height: 8em !important; */
}

#menuBtn {
  background-color: #144441 !important;
}
#navbarTitle {
  color: white !important;
  border-bottom: 5px solid #04baad;
}
.navText {
  color: white !important;
}

#descriptionComponent {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2em;
}
#descImages {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.descImgContainer {
  width: 100%;
  height: 100%;
}
.descImg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
#descTitle {
  font-size: 3em;
  border-bottom: 5px solid #04baad;
}
#descText {
  font-size: 1.2em;
}

.infoImgContainer {
  height: 100%;
  width: 100%;
}
.infoImg {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
.infoTextGroup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* border: 1px solid black; */
  height: 100%;
  width: 100%;
}
.infoTitle {
  color: white;
  background-color: black;
  font-size: 5em;
  border-bottom: 5px solid #04baad;
}
.infoPara {
  color: white;
  background-color: black;
  font-size: 1.2em;
}

#roadmapComponent{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:2em 0 2em 0}#roadmapTitle{font-size:5em;border-bottom:5px solid #04baad}.phaseTitle{font-size:3em;border:2px dashed #04baad;margin:1em;padding:10px}.roadPara{font-size:1em;line-height:1.75em;border-top:3px solid;border-image:linear-gradient(to right, #5eecb6 0%, #081fa5 100%);border-image-slice:1;border-width:3px;margin:0 3em 0 3em;padding:40px;counter-increment:section;position:relative;color:#fff;width:90%;text-align:center;font-size:2em}.roadPara:nth-child(odd){border-right:3px solid;padding-left:0}.roadPara:nth-child(odd):before{left:100%;margin-left:-20px}.roadPara:nth-child(even){border-left:3px solid;padding-right:0}.roadPara:nth-child(even):before{right:100%;margin-right:-20px}.roadPara:first-child{border-top:0;border-top-right-radius:0;border-top-left-radius:0}.roadPara:last-child{border-bottom-right-radius:0;border-bottom-left-radius:0}
#aboutComponent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  height: 100%;
  width: 100%;
  padding: 0 10px 0 10px;
  margin-bottom: 2em;
}
#aboutTitle {
  font-size: 5em;
  border-bottom: 5px solid #04baad;
}
.aboutPara {
  font-size: 1.2em;
}

#footerComponent {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  background-color: #04baad;
  color: white;
  /* border-top: 2px solid white; */
}

#faqContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px dashed #04baad;
  margin-bottom: 5em;
  margin-top: 5em;
}
#faqGeneralTitle {
  border-bottom: 5px solid #04baad;
  font-size: 5em;
}
.faqDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2em;
}
.faqTitle {
  font-size: 3.5em;
}
.faqPara {
  font-size: 2em;
}

#landingComponent {
  height: 100%;
  width: 100%;
}
#landingText {
  height: 100%;
  width: 100%;
  /* border: 2px solid brown; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
}
#landingImgContainer {
  height: 100%;
  width: 100%;
}
#landingImg {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
#landingTitle {
  font-size: 3.2em;
  border-bottom: 5px solid #04baad;
}
#landingPara {
  font-size: 1.3em;
}
#connectBtn {
  background-color: #04baad;
  font-size: 3em;
  font-weight: 500;
}
.walletPara {
  color: #04baad;
  font-weight: 900;
  font-size: 1.2em;
}
#mintBtn {
  background-color: #04baad;
  font-size: 2em;
  font-weight: 500;
  color: white;
}

